<template>
  <div id="container" class="container">
      <h2>Proyectos</h2>
      <img src="https://avatars.githubusercontent.com/u/81430614?v=4" alt="Avatar de Yisu12" width="75" loading="lazy" class="image">
      <br><br>
      <h3>Yisu12</h3>
      <hr>
      <loading v-if="load"/>
      <div class="cards" v-for="proyecto in proyectos" :key="proyecto.id">
        <Card :name="proyecto.name" 
              :description="proyecto.description" 
              :owner="proyecto.owner.login" 
              :url="proyecto.html_url"
              :homepage="`https://${proyecto.owner.login}.github.io/${proyecto.name}`"/>
      </div>
  </div>
</template>

<script>

import Card from "./Card";
import Loading from './Loading.vue';

export default {

    data(){
        return{
            proyectos: null,
            load: false,
        }
    },
    components: {
        Card,
        Loading,
    },
    mounted(){
        this.getProjects();
    },
    methods:{
        async getProjects(){
            this.load = true;
            const res = await fetch("https://api.github.com/users/Yisu12/repos");
            const data = await res.json();
            this.proyectos = data;
            this.load = false;
        }
    },
}
</script>

<style scoped>

    .container{
        overflow: hidden;
        border: solid 1px #eee;
        box-shadow: 1px 1px 4px #333;
        text-align: center;
    }

    .cards{
        display: inline-flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .image{
        border-radius: 50%;
    }

</style>